@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Nunito+Sans:opsz@6..12&family=Varela&display=swap');

.modal-container {
    animation-name: fadeIn;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
    font-size: 17px;
    position: fixed;
    background-color: #f9c667;
    z-index: 9999;
    top: 50%;
    left: 50%;
    width: 300px;
    height: 470px;
    border-radius: 5px;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 10px rgba(0,0,0,0.2);
    padding: 10px;
  }
  
  .modal-close-btn:hover {
    cursor: pointer;
  }
  
  .modal-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .modal-title {
    margin-top: 5px;
    margin-left:100px;
    font-size: 23px;
    color: #800020;
  }
  
  .modal-form {
    height: 250px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
  }
  
  #name {
    padding: 2px;
    font-family: 'Inter', sans-serif;
    font-size: 17px;
    width: 290px;
    height: 30px;
  }
  
  .idea-lbl {
    margin-top: 10px;
  }
  #idea {
    padding: 2px;
    font-family: 'Inter', sans-serif;
    font-size: 17px;
    width: 290px;
    height: 150px;
    resize: none;
  }
  
  .submit-btn-container {
    width: 290px;
    margin-top: 25px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .submit-btn {
    background-color: #FF6F61;
    color: white;
    font-weight: bold;
    font-size: 20px;
    padding: 10px;
    border-radius: 5px;
    border: none;
    /* border: 1px white solid; */
  }
  
  .error-msg {
    color: rgb(196, 0, 0);
    margin-bottom: 2px;
    font-weight: bold;
  }
  
  .success-msg {
    color: green;
    margin-bottom: 2px;
    font-weight: bold;
  }
  
  .submit-btn:hover {
    cursor: pointer;
  }
  
  .overlay {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
  }
  
  @keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }
  
  .more-info-btn {
    display: none;
  }

.more-info-btn {
  display: none;
}

.more-info-btn:hover, .add-btn:hover {
  cursor: pointer;
}


.add-btn {
  position: absolute;
  right: 5px;
  top: 7%;
  z-index: 1000;
}

.question {
  color: white;
  width: 80vw;
}

.main-container {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

}

.question-container {
  width: 100vw;
  background-color: #708090;
  height: 20vh;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  padding: 0.5em;
  gap: 1em;
  font-family: "Varela", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: clamp(1.2em, 1.4em, 2em);
  text-align: center;
}

.question {
  padding: 0.3em;
}

.option-container {
  width: 100vw;
  height: 80vh;
  position: relative;
  background-color: blue;
  display: flex;
  font-family: "Varela", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: clamp(1.8em, 2em, 2.5em);
}

.left-side-container {
  width: 50vw;
  color: white;
  background-color: black;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

.left-option-container-nh, .right-option-container-nh, .left-option, .right-option {
  /* width: 25vw; */
  text-align: center;
  width: max(300px, 30vw);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.right-side-container {
  width: 50vw;
  background-color: white;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
}

.right-side-container:hover, .left-side-container:hover {
  cursor: pointer;
}


@media (max-width: 820px) {
    .question-container {
        height: 30vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0px;
      }
  .question {
    width: 90vw;
  }
  .add-btn {
    display: none;
  }
  
  .more-info-btn {
    display: block;
  }

  .option-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100vw;
    background-color: red;
  }

  .left-side-container {
    width: 100vw;
    height: 35vh;
  }
  .right-side-container {
    width: 100vw;
    height: 35vh;
  }
}

@media (max-width: 375px) {
    .left-side-container {
      font-size: 0.7em;
      width: 100vw;
      height: 25vh;
    }
    .right-side-container {
      font-size: 0.7em;
      width: 100vw;
      height: 30vh;
    }
  }